@media (max-width: 700px) {
	.form {
		display: flex;
		flex-direction: column;
	}
}

.SignOutBtn {
	position: absolute;
	top: 0;
	right: 0;
	margin-top: 10px;
	margin-right: 10px;
	cursor: pointer;
	padding: 10px 15px;
	background-color: transparent;
	color: white;
	border: none;
	font-size: 14px;
	border: 1px solid white;
	border-radius: 25px;
	text-transform: uppercase;
}

.dashTitle {
	width: 100%;
	text-align: center;
}

.userEmail {
	color: red;
}
.instructions {
	width: 100%;
	text-align: center;
}

@media (max-height: 734px) {
	.SignOutBtn {
		font-size: 9px;
	}
}

.table img {
	width: 80%;
	margin: 0 9px;
}
.table img:hover {
	-webkit-filter: brightness(60%);
	        filter: brightness(60%);
	cursor: pointer;
}

.table {
	display: flex;
	width: 80%;
	flex-direction: column;
	margin: auto;
}
.row {
	width: 100%;
	text-align: center;
	margin: 9px auto;
	display: flex;
}

.imgholder {
	text-align: center;
	display: flex;
	flex-direction: column;
}

